import request from '@/utils/request'

export function getAllCategoryList(pageNum,pageSize) {
  return request({
    url:'/manager/category',
    method:'get',
    params:{
      pageNum,
      pageSize
    }
  })
}

export function updateProductCate(data) {
  return request({
    url: '/manager/updateCategory',
    method:'post',
    data:data
  })
}

export function getProductCate(id) {
  return request({
    url:'/manager/getCategoryId',
    method:'get',
    params:{
      id
    }
  })
}

export function createProductCate(name) {
  return request({
    url:'/manager/addCategory',
    method:'post',
    params:{
      categoryName: name
    }
  })
}

export function deleteProductCate(id) {
  return request({
    url:'/manager/deleteCat?categoryId='+id,
    method:'post'
  })
}






export function updateShowStatus(data) {
  return request({
    url:'/productCategory/update/showStatus',
    method:'post',
    data:data
  })
}

export function updateNavStatus(data) {
  return request({
    url:'/productCategory/update/navStatus',
    method:'post',
    data:data
  })
}





